.details {
    background-color: rgb(101, 164, 164) ;
    width: 100%;
    padding: 0px 20px;
    border-radius: 5px;
    color: rgb(86, 171, 171);
}

.span {
    color: rgb(63, 134, 134) ;
    margin-right: 10px;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: rgba(224, 223, 223, 0);
}

.textarea {
    border: 0;
}

.Upp {
    margin-right: 15px;
}
.Low {
    margin-right: 15px;
}
.Del {
    margin-right: 15px;
}
.Copy {
    margin-right: 15px;
}
.Cut {
    margin-right: 15px;
}
.cop-del {
    display: inline;
}


@media (max-width: 750px) {
    
    .btns {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .cop-del {
        display: flex;
        flex-direction: row;
        width: 90%;
    }
    .Upp {
        width: 90%;
        margin-bottom: 15px;
    }
    .Low {
        width: 90%;
        margin-bottom: 15px;
    }
    .Del {
        width: 90%;
        margin-bottom: 15px;
    }
    .Copy {
        width: 90%;
        margin-bottom: 15px;
    }
    .Cut {
        width: 90%;
        margin-bottom: 15px;
    }
}